<template>
  <div class="deposit-page-step-2">
    <DepositContactSupport v-if="error" />
    <template v-else-if="loading">
      <Loader width="146px" height="146px" />
      <Button
        muted
        class="deposit-page-step-2__review"
        @click="loading = false"
      >
        <Text size="14px" mr="12px">{{ tp('reviewDetails') }}</Text>
        <Icon name="arrow-down" />
      </Button>
    </template>
    <DepositPageInfo v-else />
    <Container bordered box-shadow class="deposit-page-step-2__email">
      {{ email }}
      <Tooltip
        class="deposit-page-step-2__email-notify"
        text="Email Notifications"
        :show-arrow="false"
      >
        <Icon name="notify-bell" />
      </Tooltip>
    </Container>
  </div>
</template>

<script>
import { ref } from 'vue'
import { useGetters, useMutations } from '@/plugins/vuex'
import { MODULES_NAMES } from '@/common/modules'
import DepositPageInfo from '@/views/DepositPage/DepositPageInfo'
import Container from '@/components/Container'
import Button from '@/components/UIKit/Button'
import Loader from '@/components/UIKit/Loader/Loader'
import Icon from '@/components/Icon/Icon'
import Text from '@/components/Text/Text'
import DepositContactSupport from '@/views/DepositPage/DepositContactSupport'
import { useRoute } from 'vue-router'
import Tooltip from '@/components/UIKit/Tooltip'
import { useI18n } from '@/plugins/i18n'

export default {
  name: 'Step2',
  components: { Tooltip, DepositContactSupport, DepositPageInfo, Text, Button, Container, Loader, Icon },
  setup () {
    const { tp } = useI18n('depositPage')
    const route = useRoute()
    const {
      getEmail: email
    } = useGetters(MODULES_NAMES.EXCHANGE)
    const {
      SET_TOOLS: setTools
    } = useMutations(MODULES_NAMES.TOOLS)

    setTools([
      {
        icon: {
          name: 'copy',
          width: '20px',
          height: '20px'
        },
        copyUrl: true
      },
      {
        icon: {
          name: 'info-circle',
          width: '20px',
          height: '20px'
        },
        title: 'Info Notifications',
        component: 'InfoNotifications'
      }
    ])

    const loading = ref(true)
    const error = ref(!!route.query.error)

    return {
      tp,
      loading,
      error,
      email
    }
  }
}
</script>

